import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import LinearProgress from "@mui/material/LinearProgress";

const dispatchedEvents = [];

const SrsMuiLinearProgress = function (props, ref) {
  return (
    <LinearProgress
      variant={props.variant}
      value={props.value}
      color={props.color}
      bufferValue={props.bufferValue}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiLinearProgress component */
`;

const props = [
  {
    name: "variant",
    defaultValue: "indeterminate",
    type: "string",
  },
  {
    name: "value",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "color",
    defaultValue: "primary",
    type: "string",
  },
  {
    name: "bufferValue",
    defaultValue: null,
    type: "number",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiLinearProgress),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
